





















import Vue from 'vue'
import { PropValidator } from 'vue/types/options'
import Wizard from '@/model/wizards/Wizard'
export default Vue.extend({
  props: {
    wizards: {
      type: Array,
      required: true
    } as PropValidator<Wizard[]>
  },
  methods: {
    navigateTo(wizard: Wizard) {
      this.$router.push({
        name: 'Warband',
        params: {
          wizard: wizard as any
        }
      })
    }
  }
})
